$(document).ready(function () {
  $('[data-toggle=slide]').click(function () {
    if ($('.sidebar-slide').css('background-color') == 'rgb(255, 255, 255)') {
	    $('.list-group-item').attr('tabindex', '-1');
    } else {
	    $('.list-group-item').attr('tabindex', '');
    }
    $('.row-slide').toggleClass('active');    
  });
$('.breadcrumbs-affix').affix({
  offset: {
    top: 1
  }
});
    // define the function within the global scope
    $.fn.showPopup = function() {
        this.css({opacity: 1, visibility: 'visible', "z-index": 1000});
    };
    $.fn.hidePopup = function() {
        this.css({opacity: 0, visibility: 'hidden', "z-index": 0});
    };
});